/**
 * Home Page JavaScript
 *
 * This file contains the JavaScript for the home page.
 */

// Import components
import Accordion from '../components/accordion';
import Testimonials from '../components/testimonials';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Home page initialized');
  
  // Initialize accordion for FAQ section
  Accordion.init({
    triggerSelector: '.faq-question',
    contentSelector: '.faq-answer',
    closeOthers: true,
    animateHeight: true
  });
  
  // Initialize testimonials if they exist
  const testimonials = document.querySelectorAll('.testimonial');
  if (testimonials.length > 0) {
    Testimonials.init({
      testimonialSelector: '.testimonial',
      dotSelector: '.testimonial-dot',
      activeClass: 'active',
      autoRotate: true,
      autoRotateInterval: 5000
    });
  }
  
  // Set up hero animation
  setupHeroAnimation();
}

/**
 * Set up hero animation
 */
function setupHeroAnimation() {
  const hero = document.querySelector('.hero');
  if (hero) {
    // Add subtle animation to hero section
    window.addEventListener('scroll', function() {
      const scrollPosition = window.scrollY;
      if (scrollPosition < 500) {
        const opacity = 1 - (scrollPosition / 1000);
        hero.style.opacity = Math.max(opacity, 0.7);
      }
    });
  }
}

// Initialize when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
